import { Col, Row, Image, Carousel } from "react-bootstrap"

import { useTranslation } from "react-i18next";
import useWindowDimensions from "../fonctions/getDimension.js";

import image from "../assets/images/formation.png";

import "../assets/css/study.scss";

function getStyleRow(h, w) {
    if (w < 768){
        return{
            height: h + 200 + 'px',
            width: "100%"
        }
    }
    return{
        height: h + 'px',
        width: "100%"
    }
}

function Study() {

    const { t } = useTranslation();
    
    return (
        <>
            <div id="study" style={getStyleRow(useWindowDimensions().height, useWindowDimensions().width)}>
                <Row className="text-center">
                    <Col>
                        <h1>{t('formation.title')}</h1>
                        <div className="borderTitle"> </div>
                        <p>
                            {t('formation.body.0')}<br/>
                            {t('formation.body.1')}<br/>
                            {t('formation.body.2')}
                        </p>
                    </Col>
                </Row>
                <Row>
                    <Col sm={3}>
                        <Image id="picStudy" src={image} rounded />
                    </Col>
                    <Col className="text-center" sm={9}>
                        <Carousel id="carouselStudy" className="border border-2 rounded p-4" >
                            <Carousel.Item className="p-5">
                                <h3 className="pb-2">{t('formation.lp.title')}</h3>
                                <p className="pb-3 italic">{t('formation.lp.date')}</p>
                                <p>{t('formation.lp.body')}</p>                            
                            </Carousel.Item>
                            <Carousel.Item className="p-5">
                                <h3 className="pb-2">{t('formation.dut.title')}</h3>
                                <p className="pb-3 italic">{t('formation.dut.date')}</p>
                                <p>{t('formation.dut.body')}</p>                            
                            </Carousel.Item>
                            <Carousel.Item className="p-5">
                                <h3 className="pb-2">{t('formation.staps.title')}</h3>
                                <p className="pb-3 italic">{t('formation.staps.date')}</p>
                                <p>{t('formation.staps.body')}</p>                            
                            </Carousel.Item>
                            <Carousel.Item className="p-5">
                                <h3 className="pb-2">{t('formation.licence.title')}</h3>
                                <p className="pb-3 italic">{t('formation.licence.date')}</p>
                                <p>{t('formation.licence.body')}</p>                            
                            </Carousel.Item>
                            <Carousel.Item className="p-5">
                                <h3 className="pb-2">{t('formation.bac.title')}</h3>
                                <p className="pb-3 italic">{t('formation.bac.date')}</p>
                            </Carousel.Item>
                        </Carousel>
                    </Col>
                </Row>
            </div>
        </>
            
        
    )
}

export default Study;
