import { Row, Col} from 'react-bootstrap';
import "../../assets/css/experience.scss";
import { useTranslation } from "react-i18next";

import ExperienceImage from './ExperienceImage.js';

import img1 from '../../assets/images/Experience/perso/1.png';
import img2 from '../../assets/images/Experience/perso/2.png';

const ExperiencePerso = () => {

    const { t } = useTranslation();

    var listesImages = [img1, img2];


    return (
        <>
            <Row>
                <Col className="p-4">
                    <div>
                        <h2 className="text-center mt-4">{t('experience.experience.assisthelp.title')}</h2>
                        <p className="mt-3">
                            {t('experience.experience.assisthelp.body.0')}<br/>
                            <ul>
                                <li>{t('experience.experience.assisthelp.body.1')}</li>
                                <li>{t('experience.experience.assisthelp.body.2')}</li>
                            </ul>
                        </p>
                        
                        <p className="exempleExperience text-center">
                            <a href="http://julienbedia.ddns.net/assisthelp/index.php" rel="noreferrer" target="_blank" className="boutonExperience w-20">{t('experience.experience.assisthelp.link.0')}</a><br/>
                            <a href="http://julienbedia.ddns.net/logiciel/Calculateur%20Moyenne%20V3.jar" rel="noreferrer" target="_blank" className="boutonExperience w-20">{t('experience.experience.assisthelp.link.1')}</a>
                        </p>
                        <Row className="">
                            <Col className="mt-5">
                                <p>{t('experience.experience.assisthelp.footer')}</p>
                            </Col>
                            <Col className="d-flex align-items-center justify-content-center">
                                <ExperienceImage listesImages={listesImages} />
                            </Col>
                        </Row>
                    </div>
                </Col>
            </Row>
        </>
        
    )
}

export default ExperiencePerso;
