import React from 'react'
import { useTranslation } from "react-i18next";
import { Navbar, Nav } from 'react-bootstrap';

import "../assets/css/navbar.scss";

function Navigation() {
    const { t } = useTranslation();



    return (
        <div>
            <Navbar expand="lg" variant="dark" className="fixed-top navbar" bg="dark">
                <Navbar.Toggle aria-controls="responsive-navbar-nav"/>
                <Navbar.Collapse id="responsive-navbar-nav" className="justify-content-center">
                    <Nav className="mr-auto text-center d-flex align-items-center">
                        <Nav.Item>
                            <Nav.Link id="item1" href="#study" className="navbar-link pe-3">{t('navbar.0')}</Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link id="item2" href="#experience" className="navbar-link pe-3">{t('navbar.1')}</Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link id="item3" href="#skills" className="navbar-link pe-3">{t('navbar.2')}</Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link id="item4" href="#contact" className="navbar-link pe-3">{t('navbar.4')}</Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link id="item5" href="http://julienbedia.ddns.net/CV/fichiers/BEDIAJULIENCV.pdf" className="navbar-link">{t('navbar.3')}</Nav.Link>
                        </Nav.Item>
                        
                    </Nav>
                </Navbar.Collapse>
            </Navbar>
        </div>
    )
}



export default Navigation;
