import { Row, Col} from 'react-bootstrap';
import "../../assets/css/experience.scss";
import { useTranslation } from "react-i18next";

const ExperienceWordpress = () => {

    const { t } = useTranslation();

    return (
        <>
            <Row>
                <Col className="p-4">
                    <div>
                        <h2 className="text-center mt-4">{t('experience.experience.wordpress.title')}</h2>
                        <p className="mt-3 mb-3">
                            {t('experience.experience.wordpress.body.0')}<br/>
                            {t('experience.experience.wordpress.body.1')}
                        </p>
                        <p>
                            {t('experience.experience.wordpress.body.2')}<br/>
                            {t('experience.experience.wordpress.body.3')}
                        </p>
                       
                        <Row className="">
                            <Col className="mt-5">
                                <p>{t('experience.experience.wordpress.footer')}</p>
                            </Col>
                            <Col className="d-flex align-items-center justify-content-center">
                               
                            </Col>
                        </Row>
                    </div>
                </Col>
            </Row>
        </>
        
    )
}

export default ExperienceWordpress;
