import { useState } from 'react';
import { useTranslation } from "react-i18next";
import {Col} from 'react-bootstrap';

function ExperienceImage({listesImages}){

    const { t } = useTranslation();

    const [visibility, setVisibility] = useState('hidden');
    function displayCarousel() {
        setVisibility('visible');
    }

    document.getElementById('root').onclick = function(e) {
       
        if(e.target.closest("#carousel") !== document.getElementById('carousel') && e.target.id !== "icoImage" ) {
            setVisibility('hidden');
        } else {
            setVisibility('visible');

        }
    }

    var carouselIndicator = [];

    for(let i = 1; i < listesImages.length; i++){
        carouselIndicator.push(<button type="button" data-bs-target="#carouselControls" data-bs-slide-to={i} aria-label={"Slide " + i}></button>);
    }

    return (
        <div>
            <Col className="d-flex align-items-center justify-content-center">
                {t('syntaxe.legendeImage')}<i id="icoImage" className="far fa-images iconeImage ps-2" onClick={displayCarousel}></i>
            </Col>

            <div id="carousel" className="experienceImage" style={{visibility: visibility}}>
                <div id="carouselControls" className="carousel slide " data-bs-ride="carousel">
                    <div className="carousel-indicators">
                        <button type="button" data-bs-target="#carouselControls" data-bs-slide-to="0" className="active" aria-current="true" aria-label="Slide 0"></button>
                        {carouselIndicator}
                    </div>
                    <div className="carousel-inner">
                        {listesImages.map((value, index) => {
                            if(index === 0){
                                return <div key={index} className="carousel-item active">
                                            <img src={value} className="img-thumbnail" alt="in the carousel"></img>
                                        </div>;
                            }else{
                                return <div key={index} className="carousel-item">
                                            <img src={value} className="img-thumbnail" alt="in the carousel"></img>
                                        </div>;
                            }
                            
                        })}
                    </div>
                    <button className="carousel-control-prev" type="button" data-bs-target="#carouselControls" data-bs-slide="prev">
                        <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                        <span className="visually-hidden">Previous</span>
                    </button>
                    <button className="carousel-control-next" type="button" data-bs-target="#carouselControls" data-bs-slide="next">
                        <span className="carousel-control-next-icon" aria-hidden="true"></span>
                        <span className="visually-hidden">Next</span>
                    </button>
                </div>
            </div>
        </div>
        
    )
}

export default ExperienceImage;
