import React from 'react'
import { Row, Col, Image } from 'react-bootstrap';
import { useTranslation } from "react-i18next";

import "../assets/css/contact.scss";
import image from "../assets/images/contact.png";

function Contact() {

    const { t } = useTranslation();

    function redirect(event){
        switch (event.target.id){
            case "mail":
                window.location.href = "mailto:bedia.pro@gmail.com";
                break;
            case "git":
                window.open('https://github.com/exploff', '_blank');
                break;
            case "linkedin":
                window.open('https://www.linkedin.com/in/julien-bedia-7835ba132/', '_blank');
                break;
            default:
                break;
        }
    }

    return (
        <div>
            <Row id="contact" className="text-center">
                <Col>
                    <h3 className="card-title">{t('contact.title')}</h3>
                    <Image className="picContact" src={image} rounded />
                    <p>
                        bedia.pro@gmail.com
                    </p>
                    <p className="mt-4">
                        <i id="mail" className="far fa-envelope pe-4" onClick={redirect}></i>
                        <i id="git" className="fab fa-github ps-4 pe-4" onClick={redirect}></i>
                        <i id="linkedin" className="fab fa-linkedin ps-4" onClick={redirect}></i>
                    </p>
                    <p>
                        
                    </p>
                    <p>
                        
                    </p>
                </Col>
            </Row>
        </div>
    )
}


export default Contact;
