import { Row, Col} from 'react-bootstrap';
import "../../assets/css/experience.scss";
import { useTranslation } from "react-i18next";

import ExperienceImage from './ExperienceImage.js';
import img1 from '../../assets/images/Experience/oncopole/1.png';
import img2 from '../../assets/images/Experience/oncopole/2.png';
import img3 from '../../assets/images/Experience/oncopole/3.png';


const ExperienceOncopole = () => {

    const { t } = useTranslation();
    
    var listesImages = [img1, img2, img3];

    return (
        <>
            <Row>
                <Col className="p-4">
                    <div>
                        <h2 className="text-center mt-4">{t('experience.experience.oncopole.title')}</h2>
                        <p className="mt-3">
                            {t('experience.experience.oncopole.body.0')}<br/>
                            {t('experience.experience.oncopole.body.1')}<br/>
                            <ul className="mt-2">
                                <li>{t('experience.experience.oncopole.body.2')}</li>
                                <li>{t('experience.experience.oncopole.body.3')}</li>
                                <li>{t('experience.experience.oncopole.body.4')}</li>
                            </ul>
                        </p>
                        
                        <p className="exempleExperience text-center">
                            <spans className="italic">{t('experience.experience.oncopole.subfooter')}</spans><br/>
                            <a href="https://salimkanoun.github.io/moodle_dicomviewer_website/" rel="noreferrer" target="_blank" className="boutonExperience w-12">{t('experience.experience.oncopole.link.0')}</a><br/>
                            <a href="https://pixilib.github.io/GaelO_Website/" rel="noreferrer" target="_blank" className="boutonExperience w-12">{t('experience.experience.oncopole.link.1')}</a><br/>
                        </p>
                        <Row className="">
                            <Col className="mt-5">
                                <p>{t('experience.experience.oncopole.footer')}</p>

                            </Col>
                            <Col className="d-flex align-items-center justify-content-center">
                                <ExperienceImage listesImages={listesImages} />
                            </Col>
                        </Row>
                    </div>
                </Col>
            </Row>
           
        
        </>
        
    )
}

export default ExperienceOncopole;
