import { Row, Col, Image} from 'react-bootstrap';
import "../../assets/css/experience.scss";
import { useState } from 'react'

import { useTranslation } from "react-i18next";
import ExperienceOncopole from './ExperienceOncopole';
import ExperiencePerso from './ExperiencePerso';
import ExperienceIut from './ExperienceIut';
import ExperienceNuitInfo from './ExperienceNuitInfo';
import ExperienceWordpress from './ExperienceWordpress';

import image from '../../assets/images/experience.png';

const ExperienceRoot = () => {

    const { t } = useTranslation();
    const [selectedOptionMenu, setSelectedOptionMenu] = useState('oncopole');
    const [previousItem, setPreviousItem] = useState('oncopole')

    function getComponent(){
        switch (selectedOptionMenu) {
            case 'oncopole':
                return (<ExperienceOncopole />);
            case 'projetPersonnel':
                return (<ExperiencePerso />);
            case 'projetDut':
                return (<ExperienceIut />);
            case 'nuitInfo':
                return (<ExperienceNuitInfo />);
            case 'wordpress':
                return (<ExperienceWordpress />);
            default:
                return ([]);
                
        }
    }
    
    function clickHandler(event){
      
        document.getElementById(previousItem).classList.remove('linkExperience-active'); 
        if(event.target.localName !== "i"){
            event.target.classList.add('linkExperience-active');
            setPreviousItem(event.target.value);
            setSelectedOptionMenu(event.target.value);
        }else{
            event.target.parentElement.classList.add('linkExperience-active');
            setPreviousItem(event.target.parentElement.value);
            setSelectedOptionMenu(event.target.parentElement.value);
        }
    }
    
    return (
        <div id="experience">
            <Row className="text-center">
                <Image id="picExperience" src={image} rounded />
                <Col>
                    <h1>{t('experience.title')}</h1>
                    <div className="borderTitle"> </div>
                    
                </Col>
            </Row>
            <Row className="text-center">
                <Col>
                    <p>{t('experience.body')}</p>
                </Col>
                
            </Row>
            <div className="experienceBody">
                <Row>
                    <Col sm={4} className="border-end border-2 mt-4 mb-4">
                        <nav className="menuExperience">
                            <li>
                                <button id="oncopole" type="button" value="oncopole" className="linkExperience linkExperience-active" onClick={clickHandler}>
                                    <i className="fas fa-arrow-circle-right pe-2"></i>{t('experience.menu.0')}
                                </button>
                            </li>
                            <li>
                                <button id="projetPersonnel" type="button" value="projetPersonnel" className="linkExperience" onClick={clickHandler}>
                                    <i className="fas fa-arrow-circle-right pe-2"></i>{t('experience.menu.1')}
                                </button>
                            </li>
                            <li>
                                <button id="projetDut" type="button" value="projetDut" className="linkExperience" onClick={clickHandler}>
                                    <i className="fas fa-arrow-circle-right pe-2"></i>{t('experience.menu.2')}
                                </button>
                            </li>
                            <li>
                                <button id="nuitInfo" type="button" value="nuitInfo" className="linkExperience" onClick={clickHandler}>
                                    <i className="fas fa-arrow-circle-right pe-2"></i>{t('experience.menu.3')}
                                </button>
                            </li>
                            <li>
                                <button id="wordpress" type="button" value="wordpress" className="linkExperience" onClick={clickHandler}>
                                    <i className="fas fa-arrow-circle-right pe-2"></i>{t('experience.menu.4')}
                                </button>
                            </li>
                        </nav>
                    </Col>
                    <Col sm={8}>
                        {getComponent()}
                    </Col>
                </Row>
            </div>
        </div>
        
    )
}

export default ExperienceRoot;
