import React from 'react'
import bgVideo from "../../assets/animation/bgVideo.mp4";
import useWindowDimensions from "../../fonctions/getDimension.js";

function getStyleRow(h, w) {
    if (w < 1050){
        return{
            maxWidth: "100%",
	        height: '100%'
        }
    }
    return{
        maxWidth: "100%"
    }
}

function VideoBg() {
    return (
        <>

            <video autoPlay loop muted id='video' style={getStyleRow(useWindowDimensions().height, useWindowDimensions().width)}>
                <source src={bgVideo} type="video/mp4"/>
            </video>
        </>
        
    )
}

export default VideoBg;