import { useTranslation } from "react-i18next";
import { Row, Col } from "react-bootstrap";
import "../../assets/css/shifumi.scss";

const Shifumi = (props) => {
    
    const { t } = useTranslation();


    var styleShifumi = {
        left: "-150%"
    }


    if(props.display === "yes"){
        styleShifumi.left = "50%";
    }else{
        styleShifumi.left = "-150%";
    }

    function game(choixJoueur) {
        let choixPC = Math.floor(Math.random() * Math.floor(3));

        if(choixJoueur === "1"){
            document.getElementById("resultat").innerHTML = '<p>Vous avez choisi la PIERRE</p>';
            switch (choixPC){
                case 0:
                    document.getElementById("resultat").innerHTML += '<p>Julien à choisi la PIERRE.</p><p><i class="far fa-hand-rock shifumi-signe shifumi-signe-item"></i></p><p>Vous avez fait égalité !</p>';
                    break;
                case 1:
                    document.getElementById("resultat").innerHTML += '<p>Julien à choisi la FEUILLE.</p><p><i class="far fa-hand-paper shifumi-signe shifumi-signe-item"></i></p><p>Julien remporte la partie !</p>';
                    break;												
                case 2:
                    document.getElementById("resultat").innerHTML += '<p>Julien à choisi le CISEAU.</p><p><i class="far fa-hand-scissors shifumi-signe shifumi-signe-item"></i></p><p>Vous avez gagné !</p>';
                    break;
                default:
                    break;
            }

        }else if(choixJoueur === "2"){
            document.getElementById("resultat").innerHTML = '<p>Vous avez choisi la FEUILLE</p>';
            switch (choixPC){
                case 0:
                    document.getElementById("resultat").innerHTML += '<p>Julien à choisi la PIERRE.</p><p><i class="far fa-hand-rock shifumi-signe shifumi-signe-item"></i></p><p>Vous avez gagné !</p>';
                    break;
                case 1:
                    document.getElementById("resultat").innerHTML += '<p>Julien à choisi la FEUILLE.</p><p><i class="far fa-hand-paper shifumi-signe shifumi-signe-item"></i></p><p>Vous avez fait égalité !</p>';
                    break;												
                case 2:
                    document.getElementById("resultat").innerHTML += '<p>Julien à choisi le CISEAU.</p><p><i class="far fa-hand-scissors shifumi-signe shifumi-signe-item"></i></p><p>Julien remporte la partie !</p>';
                    break;
                default:
                    break;
                    
            }

        }else if(choixJoueur === "3"){
            document.getElementById("resultat").innerHTML = '<p>Vous avez choisi le CISEAU</p>';
            switch (choixPC){
                case 0:
                    document.getElementById("resultat").innerHTML += '<p>Julien à choisi la PIERRE.</p><p><i class="far fa-hand-rock shifumi-signe shifumi-signe-item"></i></p><p>Julien remporte la partie !</p>';
                    break;
                case 1:
                    document.getElementById("resultat").innerHTML += '<p>Julien à choisi la FEUILLE.</p><p><i class="far fa-hand-paper shifumi-signe shifumi-signe-item"></i></p><p>Vous avez gagné !</p>';
                    break;												
                case 2:
                    document.getElementById("resultat").innerHTML += '<p>Julien à choisi le CISEAU.</p><p><i class="far fa-hand-scissors shifumi-signe shifumi-signe-item"></i></p><p>Vous avez fait égalité !</p>';
                    break;
                default:
                    break;
            }
        }

    }

    return (
        <div id="shifumi" className="shifumi-card text-center" style={styleShifumi}>

            <h3 className="card-title mt-4">{t('shifumi.title')}</h3>
            <Row className="mt-3 shifumi-signe">
                <Col>
                    <i class="far fa-hand-rock shifumi-signe-item ps-5" onClick={() => game("1")}></i>
                </Col>
                <Col>
                    <i class="far fa-hand-paper shifumi-signe-item" onClick={() => game("2")}></i>
                </Col>
                <Col>
                    <i class="far fa-hand-scissors shifumi-signe-item pe-5" onClick={() => game("3")}></i>
                </Col>
            </Row>
            <Row>
                <Col>
                    <div id="resultat" className="mt-3"></div>
                </Col>
            </Row>
        </div>
    )
}


export default Shifumi;
