import { Container, Row } from 'react-bootstrap';
import Lang from "./components/Lang.tsx";
import Header from "./components/Header/Header.js";
import Study from "./components/Study.js";
import ExperienceRoot from "./components/Experience/ExperienceRoot.js";
import Skills from "./components/Skills.js";
import VideoBg from "./components/Header/VideoBg.js";
import Contact from "./components/Contact.js";
import Footer from "./components/Footer.js";
import Arrow from "./components/Arrow.js";
import Navbar from "./components/Navigation.js";

import { useState } from 'react';
import i18n from "i18next";
import { initReactI18next } from "react-i18next";
import LanguageDetector from 'i18next-browser-languagedetector';
import { Language } from "./enums/Language";
import translationFR from './lang/fr.json';

let defaultLanguage = Language.FR;
 
// the translations
const resources = {
  fr: {
    translation: translationFR
  }
};
 
i18n
  .use(LanguageDetector)
  .use(initReactI18next) // passes i18n down to react-i18next
  .init({
    resources,
    lng: defaultLanguage,
 
    keySeparator: ".",  // to support nested translations
 
    interpolation: {
      escapeValue: false // react already safes from xss
    }
  });
 
function App() {

  const [lang, setLang] = useState('EN');
  
  function changeLanguage(language){
    setLang(language);
    i18n.changeLanguage(language)
  }
  

  return (
    <div>
      <VideoBg />
      <Navbar />
      <Container fluid>
        <Row>
            <Arrow />
            <Header />
            <Lang onLanguageChange = {changeLanguage} language = {lang} />
            <Study />
            <ExperienceRoot />
            <Skills />
            <Contact />
            <Footer />
        </Row>
      </Container>
    </div>
  );
}

export default App;
