import { Col, Image } from "react-bootstrap"
import useWindowDimensions from "../../fonctions/getDimension.js";
import { useState } from 'react'
import image from "../../assets/images/principal.png";

import { useTranslation } from "react-i18next";
import Shifumi from './Shifumi.js';

function getStyleRow(h, w) {
    return{
        height: h + 'px',
        width: "100%"
    }
}


function Header() {
    
    const [display, setDisplay] = useState('no');

    const { t } = useTranslation();

    function displayShifumi(){
        if (display === "yes"){
            setDisplay("no");
        }else{
            setDisplay("yes");
        }
    }

    document.getElementById('root').onclick = function(e) {
       
        if(e.target.closest("#shifumi") !== document.getElementById('shifumi') && e.target.id !== "icoShifumi") {
            setDisplay("no");
        } else {
            setDisplay("yes");

        }
    }

    return (
        <>
            <div id="header" className="" style={getStyleRow(useWindowDimensions().height, useWindowDimensions().width)}>
            </div>
            <Col>
                <Image id="icoShifumi" className="picShifumi" src={image} onClick={displayShifumi} rounded />
                <Shifumi display={display} />
                <div className="styleHeader text-center">
                    <h1>Julien Bedia</h1>
                    <p>{t('header.body')}</p>
                </div>
            </Col>
        </>
            
        
    )
}

export default Header;
