import { Row, Col} from 'react-bootstrap';
import "../../assets/css/experience.scss";
import { useTranslation } from "react-i18next";

import ExperienceImage from './ExperienceImage.js';

import img1 from '../../assets/images/Experience/iut/1.png';
import img2 from '../../assets/images/Experience/iut/2.png';
import img3 from '../../assets/images/Experience/iut/3.png';
import img4 from '../../assets/images/Experience/iut/4.png';
import img5 from '../../assets/images/Experience/iut/5.png';
import img6 from '../../assets/images/Experience/iut/6.png';

const ExperienceIut = () => {

    const { t } = useTranslation();

    var listesImages = [img1, img2, img3, img4, img5, img6];


    return (
        <>
            <Row>
                <Col className="p-4">
                    <div>
                        <h2 className="text-center mt-4">{t('experience.experience.iutprojet.title')}</h2>
                        <p className="mt-3">
                            {t('experience.experience.iutprojet.body.0')}<br/>
                            <ul>
                                <li>{t('experience.experience.iutprojet.body.1')}</li>
                                <li>{t('experience.experience.iutprojet.body.2')}</li>
                            </ul>
                        </p>
                        
                        <p className="exempleExperience text-center">
                            <a href="http://julienbedia.ddns.net/logiciel/Simpson2D.jar" rel="noreferrer" target="_blank" className="boutonExperience w-17">{t('experience.experience.iutprojet.link.0')}</a><br/>
                            <a href="http://julienbedia.ddns.net/logiciel/JeuMastermind.jar" rel="noreferrer" target="_blank" className="boutonExperience w-17">{t('experience.experience.iutprojet.link.1')}</a><br/>
                            <a href="http://julienbedia.ddns.net/logiciel/JeuPuissance4V1.jar" rel="noreferrer" target="_blank" className="boutonExperience w-17">{t('experience.experience.iutprojet.link.2')}</a><br/>
                            <a href="http://julienbedia.ddns.net/logiciel/JeuYamsV1.jar" rel="noreferrer" target="_blank" className="boutonExperience w-17">{t('experience.experience.iutprojet.link.3')}</a>
                        </p>
                        <Row className="">
                            <Col className="mt-5">
                                <p>{t('experience.experience.iutprojet.footer')}</p>
                            </Col>
                            <Col className="d-flex align-items-center justify-content-center">
                                <ExperienceImage listesImages={listesImages} />
                            </Col>
                        </Row>
                    </div>
                </Col>
            </Row>
        </>
        
    )
}

export default ExperienceIut;
