import { Col, Row, Image } from "react-bootstrap"

import { useTranslation } from "react-i18next";

import image from "../assets/images/competence.png";
import "../assets/css/skills.scss";

function Skills() {

    const { t } = useTranslation();
    
    return (
        <>
            <div id="skills">
                <Row className="text-center">
                    <Col>
                        <h1>{t('skills.title')}</h1>
                        <div className="borderTitle"> </div>
                        <p>
                            {t('skills.body')}
                        </p>
                    </Col>
                </Row>

                <Row className="text-center mt-5">
                    <Col className="skills">
                        <h3><i class="fas fa-laptop-code me-3"></i>{t('skills.type.0')}</h3>
                        <p className="text-start ms-5 mt-5">
                        
                            <h4>{t('skills.technologies.0')}</h4>
                            <ul className="mb-3">
                                <li><i class="fas fa-star-of-life me-3"></i>Java</li>
                                <li><i class="fas fa-star-of-life me-3"></i>C / C++</li>
                            </ul>
                            <h4>{t('skills.technologies.1')}</h4>
                            <ul className="mb-3">
                                <li><i class="fas fa-star-of-life me-3"></i>PHP</li>
                                <li><i class="fas fa-star-of-life me-3"></i>HTML / CSS / BootStrap</li>
                                <li><i class="fas fa-star-of-life me-3"></i>JavaScript / ReactJS</li>
                            </ul>
                            <h4>{t('skills.technologies.2')}</h4>
                            <ul className="mb-3">
                                <li><i class="fas fa-star-of-life me-3"></i>MySQL</li>
                                <li><i class="fas fa-star-of-life me-3"></i>PL/SQL</li>
                            </ul>
                            <h4>{t('skills.technologies.3')}</h4>
                            <ul className="mb-3">
                                <li><i class="fas fa-star-of-life me-3"></i>GitHub</li>
                                <li><i class="fas fa-star-of-life me-3"></i>Logiciel Graphique</li>
                            </ul>
                        </p>
                    </Col>
                    <Col className="skills">
                        <h3><i class="fas fa-user-circle me-3"></i>{t('skills.type.1')}</h3>
                        <p className="text-start ms-5 mt-5">
                            <h5>{t('skills.comportementale.0')}</h5>
                            <h5>{t('skills.comportementale.1')}</h5>
                            <Image className="picSkills" src={image} rounded />
                            <h5>{t('skills.comportementale.2')}</h5>
                            <h5>{t('skills.comportementale.3')}</h5>
                            <h5>{t('skills.comportementale.4')}</h5>
                            <h5>{t('skills.comportementale.5')}</h5>
                        </p>
                    </Col>
                </Row>
            </div>
        </>
            
        
    )
}

export default Skills;
